<template>
  <AppHeaderDropdown right no-caret>
    <template slot="header">
      <span class="text-avatar">Hi, {{user.username}}</span>
      <img
        src="img/avatars/user_icon.png"
        class="img-avatar"
        alt="admin@bootstrapmaster.com" />
    </template>
    <template slot="dropdown">
      <b-dropdown-header tag="div" class="text-center"><strong>Account</strong></b-dropdown-header>
      <b-dropdown-item><i class="fa fa-user" /> Profile</b-dropdown-item>
      <b-dropdown-item><i class="fa fa-wrench" /> Settings</b-dropdown-item>
      <b-dropdown-divider />
      <!--<b-dropdown-item><i class="fa fa-lock" router-link to="/page-login"/> Logout</b-dropdown-item>-->
      <b-dropdown-item @click="logout()"><i class="fa fa-lock"/> Logout</b-dropdown-item>
    </template>
  </AppHeaderDropdown>
</template>

<script>
import { HeaderDropdown as AppHeaderDropdown } from '@coreui/vue'
import {mapState} from "vuex";
export default {
  name: 'DefaultHeaderDropdownAccnt',
  components: {
    AppHeaderDropdown
  },
  computed: mapState({
    user: state => state.user
  }),
  methods: {
    logout () {
      this.$store.dispatch('logout', { username: this.user.username})
        .then(() => this.$router.push('/login'))
    },
  },
}
</script>
